@charset "UTF-8";
/* CSS Document */

body {
  margin: 0;
  padding: 0;
  /* background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab); */
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
  height: 100vh;
	/* height: 100vh; */
  
  /* background-color: #eeeeee; */
  /* background: hsl(var(--cr_big-stone)); */
}
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
.wrapper-place {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: red;
  opacity: 1; /* Firefox */
}

.logo {
  float: left;
}
/* ~~ Top Navigation Bar ~~ */

#navigation-container {
  width: 1200px;
  margin: 0 auto;
  height: 70px;
}

.navigation-bar {
  background: rgba(0, 0, 0, 0.5);
  height: 6rem;
  width: 100%;
  text-align: center;

  position: fixed; /* Set the navbar to fixed position */
  top: 0; /* Position the navbar at the top of the page */
}
.navigation-bar img {
  float: left;
  width: 70px;
  padding: 10px;
}
.navigation-bar ul {
  padding: 0px;
  margin: 0px;
  text-align: center;
  display: inline-block;
  vertical-align: top;
}

.navigation-bar li {
  list-style-type: none;
  padding: 0px;
  height: 24px;
  margin-top: 4px;
  margin-bottom: 4px;
  display: inline;
}
.inline-block {
  display: inline-block;
}

.navigation-bar li a {
  color: white;
  font-size: 14px;
  letter-spacing: 3px;
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 70px;
  padding: 5px 35px;
  opacity: 0.7;
}

.wrapper {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
}
.wrapper-buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.wrapper-name-guest {
  margin-bottom: 1rem;
}
.empty-div {
  height: 13.5rem;
}

.navigation-bar li a:hover {
  font-weight: 500;
}

.text-name {
  color: white;
  size: 1;
  font-size: 3rem;
  font-family: cursive;
}
.text-date {
  color: white;
  size: 1;
  font-size: 2rem;
  font-family: cursive;
}
#menu {
  float: right;
}

.container {
  background-image: url("../wedding.jpg");
  background-attachment: fixed;
  position: unset;
  top: 0;
  overflow: scroll;
  background-repeat: no-repeat;
  width: 100%;
  border-radius: 1rem;
  animation: fadeInBackground 2s ease-in-out;

}
.wedding-page {
  background-image: url("../doronStasea.png");
  background-attachment: fixed;
  position: unset;
  top: 0;
  overflow: scroll;
  background-repeat: no-repeat;
  width: 100%;
  border-radius: 1rem;
  animation: fadeInBackground 2s ease-in-out;

}
.wedding-logo {
  background-image: url("../S-D.png");
  background-attachment: fixed;
  position: unset;
  height: 100%;
  top: 0;
  overflow: scroll;
  background-repeat: no-repeat;
  width: 100%;
  border-radius: 1rem;


}



@keyframes fadeInBackground {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.size-container-before {
  height: 70rem;
  background-size: 24rem 41.6rem;
}
.size-container-after {
  height: 30rem;
  background-size: 24rem 30rem;
}
@keyframes fadeOutBackground {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
  }
}
.container:active{
  animation: fadeOutBackground 2s ease-in-out;

}

