:root {
  /* colors */
  --cr_white: 0, 0%, 100%;
  --cr_shuttle-gray: 215, 15%, 39%;
  --cr_puerto-rico: 171, 76%, 45%;
  --cr_big-stone: 216, 33%, 18%;

  /* fonts */
  --ft-fam-primary: "Poppins", sans-serif;
  --ft-wt-500: 500;
  --ft-se-400: 0.9rem;

  /* size */
  --size-200: 0.5em;
  --size-300: 0.8em;
  --size-400: 1.5em;

  /* transition */
  --tn-1: 180ms linear;

  /* components */
  --page_cr: hsl(var(--cr_white));
  --page_bd: hsl(var(--cr_big-stone));
  --page_font: var(--ft-fam-primary);

  --form__input_border-cr: hsl(var(--cr_shuttle-gray));
  --form__input_border-cr_active: hsl(var(--cr_puerto-rico));
  --form__input_txt-cr: hsl(var(--cr_white));
}

.page {
  /* color */
  color: var(--page_cr);
  /* background: var(--page_bd); */

  /* grid */
  display: grid;
  align-items: center;

  /* size */
  min-height: 100vh;

  /* typography */
  font-family: var(--page_font);
}

.page__form {
  /* position */
  margin-inline: auto;

  /* size */
  max-width: 20em;
}

.form {
  /* color */
  color: var(--page_cr);
  /* background: var(--page_bd); */

  /* grid */
  display: flex;
  gap: var(--size-300);

  /* size */
  width: 100%;
  position: relative;
  top: 3rem;
}

.form__linput {
  /* color */
  color: var(--form__input_border-cr);

  /* grid */
  display: grid;

  /* position */
  position: relative;
}

.form__input {
  /* color */
  /* color: white; */
  background: inherit;

  /* size */
  padding: var(--size-300);
  /* width: 100%; */
  border: 2px solid;
  border-radius: var(--size-200);

  /* typography */
  font-weight: var(--ft-wt-500);
  height: 6px;
}

.form__input:valid,
.form__input:focus {
  /* size */
  border: 2px solid var(--form__input_border-cr_active);
}

.form__input:valid + .form__label,
.form__input:focus + .form__label {
  /* typography */
  font-size: var(--ft-se-400);
}

.form__input:valid + .form__label,
.form__input:focus + .form__label {
  /* color */
  color: var(--form__input_border-cr_active);

  /* position */
  top: -57%;
}

.form__input:valid + .form__label::before,
.form__input:focus + .form__label::before {
  /* size */
  height: 90%;

  /* visual */
  opacity: 1;
}

.form__label {
  /* color: white; */
  /* animation */
  transition: top var(--tn-1), font-size var(--tn-1);
  position: absolute;
  left: 1.9em;
  top: 50%;
  transform: translateY(-50%);
  padding-inline: var(--size-200);
  -webkit-user-select: none;
  user-select: none;
  cursor: text;
  font-weight: var(--ft-wt-500);
  letter-spacing: 1px;
  font-size: 12px;
}

.form__label::before {
  /* animation */
  transition: height 90ms linear 90ms, opacity var(--tn-1);

  /* position */
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0%;

  /* size */
  width: 100%;
  height: 0;
  border-inline: 2px solid var(--form__input_border-cr_active);

  

  /* visual */
  opacity: 0;
  content: "";
}


